@media screen and (max-width: 1050px) {
    .topbar_box {
        padding: 22px 20px !important;
    }
}

@media screen and (max-width: 860px) {
    .topbar_box {
        padding: 14px 4px !important;
    }
    .topbar_text {
        font-size: 16px !important;
    }
    #web_link_box div {
        margin: 0px 4px !important;
        width: 25px !important;
        height: 25px !important;
        border-radius: 4px !important;
    }
}

@media screen and (max-width: 620px) {
    .topbar_text {
        width: 60px !important;
    }
    .topbar_box {
        padding: 12px 2px !important;
    }
    .topbar_text {
        font-size: 12px !important;
    }
    #web_link_box {
        padding: 0px 6px !important;
    }
    #web_link_box div {
        margin: 0px 4px !important;
        width: 20px !important;
        height: 20px !important;
        border-radius: 4px !important;
    }
}

#web_topbar {
    display: flex;
    justify-content: space-between
}

#web_topbar_box {
    position: relative;
    display: flex;
}

.topbar_box {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 22px 45px;
    cursor: pointer;
    user-select: none;
}

.topbar_box:hover {
    background-color: #4e4e4e;
}

.topbar_box:active {
    background-color: #313131;
}

.topbar_text {
    z-index: 2;
    font-size: 24px;
    width: 96px;
    text-align: center;
    color: white;
}

#topbar_slide_bg {
    position: absolute;
    z-index: 2;
    bottom: 0;
    height: 10%;
    width: 25%;
    background-color: #4EA3AF;
    transition: 0.1s;
}

#web_link_box {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 17px;
}

#link_plurk {
    background-image: url("../image/plurk.png");
}

#link_discord {
    background-image: url("../image/discord.png");
}

#link_youtube {
    background-image: url("../image/youtube.png");
}

#link_twitter {
    background-image: url("../image/twitter.png");
}

#link_fb {
    background-image: url("../image/fb.png");
}

#web_link_box div {
    margin: 0px 7px;
    width: 40px;
    height: 40px;
    border-radius: 8px;
    cursor: pointer;
    background-size: 100% 100%;
    transition: 0.1s;
}

#web_link_box div:hover {
    transform: scale(1.05);
    transition: 0.1s;
}

#web_link_box div:active {
    transform: scale(0.95);
    transition: 0.1s;
}