body {
    margin: 0;
    font-family: gensen, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url("./page/image/bg_white_dot.png");
    background-color: #FFA370;
    background-repeat: repeat;
    background-size: 1.2%;
    animation: bg_ani 2s linear infinite;
}

@keyframes bg_ani {
    0% {
        background-position: 0px 0px;
    }
    100% {
        background-position: 23px 23px;
    }
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
    font-family: gensen;
    src: url("./font/gensen/GenSenRounded-R.ttc");
}

@font-face {
    font-family: gensen;
    src: url("./font/gensen/GenSenRounded-M.ttc");
    font-weight: 500;
}