@media screen and (max-width: 1419px) {
    #web_bone {
        width: 100%;
    }
    .web_block {
        min-width: 0 !important;
    }
}

#web_main {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}

#web_liner_bg {
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100vh;
    background: linear-gradient(110.74deg, rgba(255, 163, 112, 0) 1.68%, #FFBB6A 44.29%, #FFA666 96.81%);
}

#web_bone {
    z-index: 2;
    display: flex;
    flex-direction: column;
}

.web_block {
    background-color: #313131;
    min-width: 1419px;
}